<script>
  import "lib-flexible"
  import BScroll from 'better-scroll'
  import Vue from 'vue'
  import axios from '../../config/http'
  import { mapActions, mapGetters } from 'vuex'
	import zxhwfooter from '../components/zxhwfooter.vue'
	export default {
    name: 'index',
    components: {
			zxhwfooter
    },
    data() {
      return {
        priNav:'ok',
        imageDisplay: [],
        sectionByUser: [],
        messageCenter: [],
        privilege: [],
        privilegeAll: [],
        editPrivilege: [],
        privilegeNews: [],
        sxtq:true,
        tqsx:true,
        zxhw:true,
        ynqd:true,
        zxxx: true,
        user: JSON.parse(window.localStorage.getItem('user')),

      }
    },
    created () {
      console.log(this.user)
      console.log('this created')
      let _this = this;
      this.$http.all([this.getImageDisplay(), this.getSectionByUser(), this.getMessageCenter(), this.getPrivilege(), this.getPrivilegeAll(), this.getPrivilegeNews()])
        .then(axios.spread(function(imageDisplay, sectionByUser, messageCenter, privilege,privilegeAll, privilegeNews){
          _this.imageDisplay = imageDisplay.data.imageDisplay;

          _this.messageCenter = messageCenter.data.messageCenter;
          _this.privilege = privilege.data.privilege;
          _this.privilegeAll = privilegeAll.data.privilegeAll;
          _this.privilegeNews = privilegeNews.data.section;
          const { section } = sectionByUser.data
            console.log('sec ',section )
            // for(const cont in section){
            //   if(cont.isOpen=='on'){
            //       cont.isOpen=true
            //   }else if( cont.isOpen =='off'){
            //       cont.isOpen=false
            //   }
            // }
          _this.sectionByUser = sectionByUser.data.section;
          console.log({imageDisplay, sectionByUser, messageCenter, privilege,privilegeAll, privilegeNews})
          console.log('')
        }))

    },
    beforeMount(){
      console.log('this beforeMount')
    },
    beforeUpdate(){
      let _this = this;
      let sectionShow = this.sectionByUser
      for(const obj of sectionShow) {
        if(obj.isOpen=="off"){
          if(obj.pType==2){ //
            _this.tqsx = false;
          }else if(obj.pType==5){
            _this.zxtq = false;
          }else if(obj.pType==3){
            _this.zxxx = false;
          }else if(obj.pType==4){
            _this.ynqd = false;
          }
        }

      }
    },
    updated () {
      console.log(this.imageDisplay);
    },
    methods: {
      removetoken(){
        // if(window.localStorage.getItem('token')){
        //   window.localStorage.removeItem('token');
        // }

      },
       getImageDisplay(){
        return this.$get('guanjia_heard/homepage/image/display/getImageDisplay')
       },
       getSectionByUser(){
        return this.$get('guanjia_heard/homepage/privilege/getSectionByUser')
       },
       getMessageCenter() {
        return this.$get('guanjia_heard/homepage/message/center/getMessageCenter')
       },
       getPrivilege(){
        return this.$get('guanjia_heard/homepage/privilege/getPrivilege')
       },
       getPrivilegeAll(){
        return this.$get('guanjia_heard/homepage/privilege/getPrivilegeAll')
       },

       getPrivilegeNews(){
         return this.$get('guanjia_heard/homepage/privilege/getPrivilegeNews')
       },
       jobList: function () {
				var that = this;
				Vue.nextTick(() => {
					that.wrapper = new BScroll(that.$refs.wrapper, {
						scrollX : true,
						scrollY: false,
						click: true
					})
					that.zxhw = new BScroll(that.$refs.zxhw, {
						scrollX : true,
						scrollY: false,
						click: true
          })
          that.cards = new BScroll(that.$refs.cards, {
            scrollX : true,
            click: true,
            snap: {
              loop: true, // 
              stepX: 720, //  200px
              stepY: 400, //  100px
              threshold: 0.1, // / 30% 
              speed: 10 //  400ms
            }
          })
				})
			}
		},
		mounted: function () {
			this.jobList();
		}

  }


</script>



